// todo
import React, { useState, useEffect, useRef } from "react";
import { useLs } from "./CONTEXT/LsContext";
import _ from "lodash";
import { FaAngleLeft, FaAngleRight } from "react-icons/fa";
import {
	Row,
	Col,
	Card,
	Button,
	CardHeader,
	CardBody,
	Badge,
	Form,
	FormGroup,
	FormFeedback,
	Label,
	Input,
} from "reactstrap";
import MyToolTip from "./MyToolTip";
import { useLng } from "./CONTEXT/LngContext";

// todo
const Spelling = (props) => {
	// ? ---
	// console.log("simple render in spelling");

	// ? ---
	const ls = useLs();
	const strings = useLng();
	const index = useRef(0);

	// ? ---
	const [hint, setHint] = useState("");
	const [wd, setWd] = useState("");
	const [check, setCheck] = useState({ v: false, i: false });

	let arr = JSON.parse(localStorage.guest);
	let spArr = _.filter(arr, "df");

	// ? ---
	// * --- էս տողը նրա համար է, որ index.current-ը ստատիկ փոփոխական է,
	// * --- իսկ spArr.length-ը փոխվում է բառարանի հետ աշխատելուց։
	// * --- index.current must be in range [0 - (spArr.length-1)]
	if (spArr.length > 0 && index.current === spArr.length) {
		index.current--;
	}

	// ? ---
	useEffect(() => {
		setWd("");
		setHint("");
		setCheck({ v: false, i: false });
	}, [ls]);

	// ? ---
	if (spArr.length === 0) {
		return (
			<div className="p-5 text-center ">
				<h5>{strings.tab3_11}</h5>
			</div>
		);
	}

	// ? ---
	return (
		<div className="col-12 col-sm-10 mx-auto pt-4 pb-3">
			<Card>
				<CardHeader tag="h5" className="text-center bg-info text-white">
					{strings.tab3_1}
				</CardHeader>

				<CardHeader>
					<Row className="mx-auto">
						<Col xs={3}>
							{strings.tab3_2}
							<Badge
								color="warning"
								data-tip={strings.tab3_22}
								data-for="cw"
							>
								{index.current + 1}
							</Badge>
							<MyToolTip id="cw" type="dark" />
						</Col>

						<Col xs={3}>
							{strings.tab3_3}
							<Badge
								color="warning"
								data-tip={strings.tab3_33}
								data-for="tw"
							>
								{spArr.length}
							</Badge>
							<MyToolTip id="tw" type="dark" />
						</Col>

						<Col xs={3}>
							{strings.tab3_4}
							<Badge
								color="success"
								data-tip={strings.tab3_44}
								data-for="ra"
							>
								{spArr[index.current].p}
							</Badge>
							<MyToolTip id="ra" type="dark" />
						</Col>

						<Col xs={3}>
							{strings.tab3_5}
							<Badge
								color="danger"
								data-tip={strings.tab3_55}
								data-for="wa"
							>
								{spArr[index.current].n}
							</Badge>
							<MyToolTip id="wa" type="dark" />
						</Col>
					</Row>
				</CardHeader>

				<CardBody>
					<Form>
						<FormGroup>
							<Label
								for="Spelling"
								size="sm"
								className="text-info"
							>
								{strings.tab3_6}
							</Label>
							<Input
								type="text"
								bsSize="sm"
								value={spArr[index.current].tr}
								disabled
								className="bg-white"
							/>
						</FormGroup>

						<FormGroup>
							<Label
								for="Spelling"
								size="sm"
								className="text-info"
							>
								{strings.tab3_7}
							</Label>
							<Input
								type="text"
								bsSize="sm"
								valid={check.v}
								invalid={check.i}
								value={wd}
								onChange={(e) =>
									setWd((prev) => e.target.value)
								}
							/>
							<FormFeedback valid>
								{strings.tab3_7_c}
							</FormFeedback>
							<FormFeedback>{strings.tab3_7_i}</FormFeedback>
						</FormGroup>

						<FormGroup>
							<Label
								for="Spelling"
								size="sm"
								className="text-info"
							>
								{strings.tab3_8}
							</Label>
							<Input
								type="text"
								bsSize="sm"
								disabled
								value={hint}
								className="bg-white"
							/>
						</FormGroup>
					</Form>

					<div className="text-center">
						<Button
							color="info"
							style={{ borderRadius: "30px 7px 7px 30px" }}
							onClick={() => {
								if (index.current > 0) {
									index.current--;
									setHint("");
									setCheck({ v: false, i: false });
									setWd("");
								}
							}}
						>
							<FaAngleLeft size={25} />
						</Button>

						<Button
							color="info"
							className="ml-1"
							onClick={() => {
								setHint(
									spArr[index.current].word[0] +
										"*".repeat(
											spArr[index.current].word.length - 1
										)
								);
							}}
						>
							{strings.tab3_9}
						</Button>

						<Button
							color="info"
							className="ml-1"
							onClick={() => {
								if (
									wd.trim() ===
									spArr[index.current].word.trim()
								) {
									spArr[index.current].p++;
									setCheck({ v: true, i: false });
								} else {
									spArr[index.current].n++;
									setCheck({ v: false, i: true });
								}

								setHint(spArr[index.current].word);
								localStorage.guest = JSON.stringify(arr);
							}}
						>
							{strings.tab3_10}
						</Button>

						<Button
							color="info"
							className="ml-1"
							style={{ borderRadius: "7px 30px 30px 7px" }}
							onClick={() => {
								if (index.current < spArr.length - 1) {
									index.current++;
									setHint("");
									setCheck({ v: false, i: false });
									setWd("");
								}
							}}
						>
							<FaAngleRight size={25} />
						</Button>
					</div>
				</CardBody>
			</Card>
		</div>
	);
};

// todo
export default Spelling;
