import { useState } from "react";
import { useLng } from "../CONTEXT/LngContext";

// todo
export default function useDnd() {
	const [word, setState] = useState({
		word: "",
		pr: "",
		pos: "",
		tr: "",
		ex: "",
		df: true,
		time: "",
		top: false,
		p: 0,
		n: 0,
	});
	const strings = useLng();

	function add_word(text) {
		if (typeof text !== "string" || text.length === 0) {
			alert(strings.tab2_8);
			return;
		}
		setState((prev) => {
			return { ...prev, word: text, time: Date.now() };
		});
	}

	return [word, add_word];
}
