// todo
import Chart from "react-google-charts";
import moment from "moment";
// * --- էս տողը պետք ա, որ moment.locale("en") -ն աշխատի։
import "moment/locale/hy-am";
import _ from "lodash";
import { useLng } from "./CONTEXT/LngContext";

// todo
function util(p_title, p_s1, p_s2) {
	// ?---
	let ls = JSON.parse(localStorage.guest);

	// ?---
	let arr = [["", ""]];
	for (let i = 0; i < 26; i++) {
		arr.push([String.fromCharCode(97 + i), 0]);
	}

	// ?---
	const maxtime = _.max(ls.map((item) => item.time));
	// * --- const maxtime1 = _.maxBy(ls, "time").time;

	// ?---
	let ob = {};

	// ?---
	ls.forEach((item) => {
		if (item.word[0] in ob) {
			ob[item.word[0]] = ob[item.word[0]] + 1;
		} else {
			ob[item.word[0]] = 1;
		}
	});

	// ?---
	for (let i = 1; i < 27; i++) {
		if (arr[i][0] in ob) {
			arr[i][1] = ob[arr[i][0]];
		}
	}

	// ?---

	const options = {
		chart: {
			title: p_title,
			subtitle:
				p_s1 +
				" " +
				ls.length +
				p_s2 +
				" " +
				moment(maxtime).format("LL"),
		},
	};

	// ?---
	return {
		op: options,
		ar: arr,
	};
}

// todo
const MyChart = () => {
	// ? ---
	const strings = useLng();
	if (strings.getLanguage() === "GB") {
		moment.locale("en");
	} else {
		moment.locale("hy-am");
	}

	// ? ---
	const options = util(strings.tab4_1, strings.tab4_2, strings.tab4_3);

	// ? ---
	return (
		<div id="chartDiv" className="col-12 col-sm-10 mx-auto pt-5 pb-3">
			<Chart
				width={"610px"}
				height={"400px"}
				chartType="Bar"
				// loader={<div>Loading Chart</div>}
				data={options.ar}
				options={options.op}
			/>
		</div>
	);
};

// todo
export default MyChart;
