// todo
import React, { useState } from "react";
import { FaAngleDown, FaAngleUp } from "react-icons/fa";
import {
	Card,
	CardHeader,
	CardBody,
	CardText,
	Button,
	Input,
	Fade,
} from "reactstrap";
import { FaBookmark, FaEdit, FaTrash, FaVolumeUp } from "react-icons/fa";
import { useLs } from "./CONTEXT/LsContext";
import Highlighter from "react-highlight-words";
import MyToolTip from "./MyToolTip";
import { useLng } from "./CONTEXT/LngContext";

// todo
function Word({ pw }) {
	// ? ---
	const ls = useLs();
	const strings = useLng();
	const [isOpen, setIsOpen] = useState(false);
	const [inputs, setInputs] = useState({
		pr: pw.pr,
		pos: pw.pos,
		tr: pw.tr,
		ex: pw.ex,
	});
	const [editMode, setEditMode] = useState(false);
	const col = pw.df ? "#ffad33" : "#00e600";
	let arr = JSON.parse(localStorage.guest);

	// ? ---
	const htext = (
		<Highlighter
			highlightClassName="h"
			searchWords={[pw.word]}
			autoEscape={true}
			textToHighlight={pw.ex}
		/>
	);

	// ? ---
	const toggle = () => {
		setIsOpen(!isOpen);
	};

	// ? ---
	function index() {
		// let arr = JSON.parse(localStorage.guest);
		for (let i = 0; i < arr.length; i++) {
			if (arr[i].time === pw.time) {
				return i;
			}
		}
	}

	// ? ---
	function del() {
		let i = index();
		arr.splice(i, 1);
		localStorage.guest = JSON.stringify(arr);
		ls[1](!ls[0]);
	}

	// ? ---
	function saveChanges() {
		setEditMode(false);
		let i = index();
		arr[i].pr = inputs.pr.trim();
		arr[i].pos = inputs.pos.trim();
		arr[i].tr = inputs.tr.trim();
		arr[i].ex = inputs.ex.trim();

		localStorage.guest = JSON.stringify(arr);
		ls[1](!ls[0]);
	}

	// ? ---
	function fav() {
		let i = index();
		arr[i].df = !arr[i].df;
		localStorage.guest = JSON.stringify(arr);
		ls[1](!ls[0]);
	}

	// ? ---
	function speak() {
		let sinth = window.speechSynthesis;
		let utterance = new SpeechSynthesisUtterance(pw.word);
		utterance.voice = sinth.getVoices()[0];
		sinth.speak(utterance);
	}

	// ? ---
	const mycardBody = (
		<div>
			<CardBody>
				<CardText>
					<small>
						<li>{pw.pr}</li>
						<li className="text-warning">{pw.pos}</li>
						<li>{pw.tr}</li>
						<li className="text-info">{htext}</li>
					</small>
				</CardText>
			</CardBody>

			<div className="btn-group btn-group-sm w-100 ">
				<button
					type="button"
					className="btn btn-dark text-secondary"
					onClick={speak}
					data-tip={strings.tab1_1}
					data-for="fvu"
				>
					<FaVolumeUp />
				</button>
				<MyToolTip id="fvu" place="bottom" type="error" />

				<button
					type="button"
					className="btn btn-dark text-secondary "
					onClick={del}
					data-tip={strings.tab1_2}
					data-for="ft"
				>
					<FaTrash />
				</button>
				<MyToolTip id="ft" place="bottom" type="error" />

				<button
					type="button"
					className="btn btn-dark text-secondary"
					onClick={() => setEditMode(true)}
					data-tip={strings.tab1_3}
					data-for="fe"
				>
					<FaEdit />
				</button>
				<MyToolTip id="fe" place="bottom" type="error" />

				<button
					type="button"
					className="btn btn-dark text-secondary"
					onClick={fav}
					data-tip={strings.tab1_4}
					data-for="fb"
				>
					<FaBookmark />
				</button>
				<MyToolTip id="fb" place="bottom" type="error" />
			</div>
		</div>
	);

	// ? ---
	const selectCol = inputs.pos ? "text-success" : null;

	// ? --
	const edcardBody = (
		<div>
			<CardBody>
				<CardText>
					<small>
						<Input
							type="text"
							value={inputs.pr}
							placeholder={strings.tab2_3}
							className="text-success mb-3"
							onChange={(e) =>
								setInputs((prev) => {
									return {
										...prev,
										pr: e.target.value,
									};
								})
							}
						/>

						<Input
							type="select"
							value={inputs.pos}
							className={selectCol}
							onChange={(e) =>
								setInputs((prev) => {
									return {
										...prev,
										pos: e.target.value,
									};
								})
							}
						>
							<option value="">{strings.tab2_4}</option>
							<option>noun</option>
							<option>verb</option>
							<option>adjective</option>
							<option>adverb</option>
							<option>pronoun</option>
							<option>preposition</option>
							<option>conjunction</option>
							<option>exclamation</option>
							<option>determiner</option>
							<option>number</option>
						</Input>

						<Input
							type="text"
							value={inputs.tr}
							placeholder={strings.tab2_5}
							className="text-success my-3"
							onChange={(e) =>
								setInputs((prev) => {
									return {
										...prev,
										tr: e.target.value,
									};
								})
							}
						/>

						<Input
							type="textarea"
							value={inputs.ex}
							placeholder={strings.tab2_6}
							className="text-success"
							onChange={(e) =>
								setInputs((prev) => {
									return {
										...prev,
										ex: e.target.value,
									};
								})
							}
						/>
					</small>
				</CardText>
			</CardBody>

			<Button
				// type="button"
				className="btn btn-info btn-sm btn-block"
				size="sm"
				onClick={saveChanges}
			>
				Save Changes
			</Button>
		</div>
	);

	// ? ---
	const varBody = editMode ? edcardBody : mycardBody;
	const cardBody = isOpen ? varBody : null;
	const cardIcon = isOpen ? (
		<FaAngleUp className="float-right text-white" />
	) : (
		<FaAngleDown className="float-right text-white" />
	);

	// ? ---
	return (
		<Card className="my-2">
			<CardHeader
				style={{
					backgroundColor: col,
					padding: "5px 10px",
				}}
				className="btn btn-sm text-left"
				onClick={toggle}
			>
				{pw.word}
				{cardIcon}
			</CardHeader>

			<Fade in={isOpen} timeout={30}>
				{cardBody}
			</Fade>
		</Card>
	);
}

// todo
export default Word;
