// todo
import React, { useEffect, useReducer } from "react";
import { FaPlay, FaPause, FaStop } from "react-icons/fa";
import { Button, Input } from "reactstrap";
import classNames from "classnames";
import MyToolTip from "./MyToolTip";
import { useLng } from "./CONTEXT/LngContext";
import { useGlobal } from "./CONTEXT/GlobalContext";

// todo
const initState = {
	play: false,
	pause: false,
	stop: false,
};

// todo
function reducer(state, action) {
	switch (action.type) {
		case "start":
			return { play: false, pause: false, stop: false };
		case "play":
			return { play: true, pause: false, stop: false };
		case "pause":
			return { play: false, pause: true, stop: false };
		case "stop":
			return { play: false, pause: false, stop: true };
		default:
			throw new Error();
	}
}

// todo
const synth = window.speechSynthesis;

// todo
export default function Player() {
	// ?---
	const [state, dispatch] = useReducer(reducer, initState);
	const strings = useLng();
	const [glob, setGlob] = useGlobal();

	// ?---
	const clNamePlay = classNames("playbtn mx-1", { active: state.play });
	const clNamePause = classNames("playbtn mx-1", { active: state.pause });
	const clNameStop = classNames("playbtn mx-1", { active: state.stop });
	let flag = false;

	// ? ---
	useEffect(() => {
		setGlob({ ...glob, f: onClickPlay });
		// eslint-disable-next-line
	}, []);

	// ? ---
	function onClickPlay(par) {
		// ? ---
		let tts =
			typeof par === "string"
				? par
				: document.getElementById("text").value;

		// ! piti active darna, mnacatsy` pasiv.    --- sinth.speaking=true
		// Արտասանությունը նոր սկսելիս՝ flag = false; synth.paused=false. Այսինքն կեսին չէինք կագնել, նորը պիտի սկսենք։
		if (!flag && tts && !synth.paused) {
			flag = true;

			let utterance = new SpeechSynthesisUtterance(tts);

			utterance.voice = synth.getVoices()[0];

			// վերջանալու դեպքում այս event-ն ա աշխատում։
			utterance.onend = function () {
				flag = false;
				dispatch({ type: "stop" });
				setTimeout(() => {
					dispatch({ type: "start" });
				}, 1000);
			};

			synth.speak(utterance);
			dispatch({ type: "play" });
		}

		if (synth.paused) {
			synth.resume();
			dispatch({ type: "play" });
		}
	}

	// ?---
	function onClickPause() {
		if (synth.speaking && !synth.paused) {
			// ! petqa active darna sa
			synth.pause();
			dispatch({ type: "pause" });
		}
	}

	// ?---
	function onClickStop() {
		// ! sa verjum kdarna aktive , bayc mi qani varkjanwv.
		if (synth.speaking) {
			synth.cancel();
			// * --- tanum a 'onend' event-y
		}
	}

	// ?---
	return (
		<div className="w-75 p-2 mx-auto">
			<Input
				type="textarea"
				className="text-success mt-4 mb-3"
				placeholder={strings.tab5_1}
				id="text"
			/>

			<div className="text-center">
				<Button
					outline
					color="info"
					className={clNamePlay}
					onClick={onClickPlay}
					data-tip={strings.tab5_2}
					data-for="FaPlay"
					style={{ boxShadow: "none" }}
				>
					<FaPlay />
				</Button>
				<MyToolTip id="FaPlay" place="left" type="warning" />

				<Button
					outline
					color="info"
					className={clNamePause}
					onClick={onClickPause}
					data-tip={strings.tab5_3}
					data-for="FaPause"
					style={{ boxShadow: "none" }}
				>
					<FaPause />
				</Button>
				<MyToolTip id="FaPause" place="bottom" type="warning" />

				<Button
					outline
					color="info"
					className={clNameStop}
					onClick={onClickStop}
					data-tip={strings.tab5_4}
					data-for="FaStop"
					style={{ boxShadow: "none" }}
				>
					<FaStop />
				</Button>
				<MyToolTip id="FaStop" place="right" type="warning" />
			</div>
		</div>
	);
}
