// todo
import React, { useState } from "react";
import { Button, Form, Input, CustomInput, FormFeedback } from "reactstrap";
import { useLng } from "./CONTEXT/LngContext";

// todo
const AddForm = () => {
	// ? ---
	const initState = {
		word: "",
		pr: "",
		pos: "",
		tr: "",
		ex: "",
		df: true,
		time: Date.now(),
		top: false,
		p: 0,
		n: 0,
	};

	const [state, setState] = useState(initState);
	const [isEmpty, setIsEmpty] = useState(false);
	const strings = useLng();

	// ? ---
	function initLocalStorage(key, value) {
		const item = localStorage.getItem(key);
		const v = item ? JSON.parse(item) : value;

		if (v.length === 0) {
			localStorage.setItem(key, JSON.stringify(v));
		}
	}

	initLocalStorage("guest", []);

	// ? ---
	// const key = function () {
	// 	return "_" + Math.random().toString(36).substr(2, 9);
	// };
	// ? ---
	function addword(e) {
		e.preventDefault();
		if (!state.word) {
			setIsEmpty(true);
			setTimeout(() => setIsEmpty(false), 5000);
			return;
		}

		const item = localStorage.getItem("guest");
		const words = JSON.parse(item);

		words.push(state);
		setState(initState);

		localStorage.setItem("guest", JSON.stringify(words));
	}

	// ? ---
	return (
		<div className="col-12 col-sm-10 mx-auto py-3">
			<Form onSubmit={addword}>
				<h5 className="text-center mb-4 " style={{ color: "#800080" }}>
					{strings.tab2_1}
				</h5>
				<Input
					className="text-success "
					placeholder={strings.tab2_2}
					value={state.word}
					onChange={(e) => {
						setState((prev) => {
							return {
								...prev,
								word: e.target.value.trim().toLowerCase(),
							};
						});
						setIsEmpty(false);
					}}
					invalid={isEmpty}
				/>
				<FormFeedback> {strings.tab2_2_1}</FormFeedback>
				<Input
					className="text-success my-3"
					type="text"
					placeholder={strings.tab2_3}
					value={state.pr}
					onChange={(e) =>
						setState((prev) => {
							return {
								...prev,
								pr: e.target.value,
							};
						})
					}
				/>
				<CustomInput
					className={state.pos ? "text-success" : ""}
					type="select"
					id="pos"
					value={state.pos}
					onChange={(e) =>
						setState((prev) => {
							return {
								...prev,
								pos: e.target.value,
							};
						})
					}
				>
					<option value="">{strings.tab2_4}</option>
					<option>noun</option>
					<option>verb</option>
					<option>adjective</option>
					<option>adverb</option>
					<option>pronoun</option>
					<option>preposition</option>
					<option>conjunction</option>
					<option>interjection</option>
					<option> article ( determiner ) </option>
					<option>numeral</option>
				</CustomInput>{" "}
				<Input
					className="text-success my-3"
					type="text"
					placeholder={strings.tab2_5}
					value={state.tr}
					onChange={(e) =>
						setState((prev) => {
							return {
								...prev,
								tr: e.target.value,
							};
						})
					}
				/>
				<Input
					className="text-success my-3"
					type="textarea"
					placeholder={strings.tab2_6}
					value={state.ex}
					onChange={(e) =>
						setState((prev) => {
							return {
								...prev,
								ex: e.target.value,
							};
						})
					}
				/>
				<Button
					style={{ backgroundColor: "#660066" }}
					size="sm"
					block
					// onClick={addword}
				>
					{strings.tab2_7}
				</Button>
			</Form>
		</div>
	);
};

// todo
export default AddForm;
