// todo
import Word from "./Word";
import { useLs } from "./CONTEXT/LsContext";
import { InputGroup, InputGroupAddon, Button, Input } from "reactstrap";
import { useState } from "react";
import { FaEye, FaEyeSlash } from "react-icons/fa";
import MyToolTip from "./MyToolTip";
import { useLng } from "./CONTEXT/LngContext";

// todo
function WordList() {
	// ? ---
	// eslint-disable-next-line
	const ls = useLs();
	const strings = useLng();
	const [filter, setFilter] = useState("");
	const [cBox, setCBox] = useState([false, false]);

	// ? ---
	if (!localStorage.guest) return null;
	let wl = JSON.parse(localStorage.guest);

	if (!wl.length) {
		alert(strings.tab_a);
		return null;
	}

	wl = wl.sort(function (a, b) {
		var nameA = a.word.toLowerCase(),
			nameB = b.word.toLowerCase();
		if (nameA < nameB)
			//sort string ascending
			return -1;
		if (nameA > nameB) return 1;
		return 0; //default return value (no sorting)
	});

	// ? --- lavn a, chjnjel
	// var ID = function () {
	// 	return "_" + Math.random().toString(36).substr(2, 9);
	// };

	// ? ---
	const returnValue = () => {
		if (cBox[0] === true && cBox[1] === true) {
			return null;
		} else if (cBox[0] === true && cBox[1] === false) {
			return wl.map((w) => {
				if (w.df === true && w.word.indexOf(filter) === 0)
					return <Word key={w.time} pw={w} />;
				return null;
			});
		} else if (cBox[0] === false && cBox[1] === true) {
			return wl.map((w) => {
				if (w.df === false && w.word.indexOf(filter) === 0)
					return <Word key={w.time} pw={w} />;
				return null;
			});
		} else if (cBox[0] === false && cBox[1] === false) {
			return wl.map((w) => {
				if (w.word.indexOf(filter) === 0) {
					return <Word key={w.time} pw={w} />;
				} else {
					return null;
				}
			});
		}
	};

	// ? ---
	return (
		<div className="col-12 col-sm-10 mx-auto py-3">
			{/* filter blok */}
			<div
				id="filter"
				className="p-1 mb-3 "
				style={{
					border: "1px solid",
					borderColor: " #734b6d",
					borderRadius: "5px",
				}}
			>
				<InputGroup size="sm">
					<InputGroupAddon addonType="prepend">
						<Button
							color="success"
							className="faicons"
							outline
							onClick={() => setCBox([!cBox[0], cBox[1]])}
							data-tip={strings.tab1_f1}
							data-for="old-word"
						>
							{cBox[0] ? (
								<FaEyeSlash size={24} />
							) : (
								<FaEye size={24} />
							)}
						</Button>
						<MyToolTip id="old-word" place="left" type="info" />
					</InputGroupAddon>

					<Input
						placeholder={strings.tab1_f3}
						className="border-0 mx-3"
						value={filter}
						onChange={(e) =>
							setFilter((prev) =>
								e.target.value.trim().toLowerCase()
							)
						}
					/>

					<InputGroupAddon addonType="append">
						<Button
							className="faicons"
							outline
							color="warning"
							onClick={() => setCBox([cBox[0], !cBox[1]])}
							data-tip={strings.tab1_f2}
							data-for="new-word"
						>
							{cBox[1] ? (
								<FaEyeSlash size={24} />
							) : (
								<FaEye size={24} />
							)}
						</Button>
						<MyToolTip id="new-word" place="right" type="info" />
					</InputGroupAddon>
				</InputGroup>
			</div>

			<div>{returnValue()}</div>
		</div>
	);
}

// todo
export default WordList;
