import "./App.css";
import React, { useState, useReducer, useEffect } from "react";
import classnames from "classnames";
import WordList from "./my-components/WordList";
import AddForm from "./my-components/AddForm";
import MyChart from "./my-components/MyChart";
import Spelling from "./my-components/Spelling";
import PlayerApp from "./my-components/PlayerApp";
import Links from "./my-components/Links";
import { BiBookAdd, BiBookReader } from "react-icons/bi";
import { FaSpellCheck, FaChartBar, FaLink, FaCog } from "react-icons/fa";
import { MdRecordVoiceOver, MdImportExport } from "react-icons/md";
import {
	TabContent,
	FormGroup,
	TabPane,
	Nav,
	NavItem,
	NavLink,
	ButtonDropdown,
	DropdownToggle,
	DropdownMenu,
	DropdownItem,
	CustomInput,
} from "reactstrap";
import MyToolTip from "./my-components/MyToolTip";
import ImportExport from "./my-components/ImportExport";
import useLocalStorage from "./my-components/HOOKS/UseLocalStorage";
import { useLng } from "./my-components/CONTEXT/LngContext";
import { useGlobal } from "./my-components/CONTEXT/GlobalContext";
import Flags from "country-flag-icons/react/3x2";
import useDnd from "./my-components/HOOKS/useDndAddWORD";
import { useLs } from "./my-components/CONTEXT/LsContext";

// todo
const initialState = {
	t2: true,
	t3: true,
	t4: true,
	t5: true,
	t6: true,
	t7: true,
};

// todo
function reducer(state, action) {
	switch (action.type) {
		case "tab2":
			return { ...state, t2: !state.t2 };
		case "tab3":
			return { ...state, t3: !state.t3 };
		case "tab4":
			return { ...state, t4: !state.t4 };
		case "tab5":
			return { ...state, t5: !state.t5 };
		case "tab6":
			return { ...state, t6: !state.t6 };
		case "tab7":
			return { ...state, t7: !state.t7 };
		default:
			throw new Error();
	}
}

// todo
function App(props) {
	// ? ---
	const [activeTab, setActiveTab] = useState("1");
	const [dropdownOpen, setOpen] = useState(false);
	const [lang, setLang] = useState(null);
	const [settings, setSettings] = useLocalStorage("settings", initialState);
	const [state, dispatch] = useReducer(reducer, settings);
	const strings = useLng();

	// eslint-disable-next-line
	const [glob, setGlob] = useGlobal();

	const [ls, setLs] = useLs();

	const [wrd, setWrd] = useDnd();

	useEffect(() => {
		if (!wrd.word) return;
		const item = localStorage.getItem("guest");
		const words = JSON.parse(item);

		// * --- sa petq e, vor miangamic ereva
		setLs(!ls);

		words.push(wrd);
		localStorage.setItem("guest", JSON.stringify(words));
		// eslint-disable-next-line
	}, [wrd.word]);

	// ? ---
	useEffect(() => {
		// console.log("will work once: at loading...");
		fetch("/get_country")
			.then((response) => response.json())
			.then((result) => {
				// console.log(result);
				if (result.country === "AM") {
					setLang(false);
				} else {
					setLang(true);
				}
			})
			.catch((err) => console.log("Error: ", err));
	}, []);

	// ? ---
	strings.setLanguage(lang ? "GB" : "AM");

	// ? ---
	const tab2 = state.t2 && (
		<NavItem
			style={{ cursor: "pointer" }}
			onDragOver={(ev) => ev.preventDefault()}
			onDrop={(ev) => {
				ev.preventDefault();
				let data = ev.dataTransfer.getData("text");
				data = data.trim().toLowerCase();
				// dndAdd(data);
				setWrd(data);
			}}
		>
			<NavLink
				className={classnames({
					active: activeTab === "2",
				})}
				onClick={() => {
					toggle("2");
				}}
			>
				<BiBookAdd size={24} data-tip={strings.tab2} data-for="bba" />
				<MyToolTip id="bba" type="info" />
			</NavLink>
		</NavItem>
	);

	const tabpen2 = state.t2 && (
		<TabPane tabId="2" className="border border-top-0">
			<AddForm />
		</TabPane>
	);

	// ? ---
	const tab3 = state.t3 && (
		<NavItem style={{ cursor: "pointer" }}>
			<NavLink
				className={classnames({
					active: activeTab === "3",
				})}
				onClick={() => {
					toggle("3");
				}}
			>
				<FaSpellCheck
					size={24}
					data-tip={strings.tab3}
					data-for="fspc"
				/>
				<MyToolTip id="fspc" type="info" />
			</NavLink>
		</NavItem>
	);

	const tabpen3 = state.t3 && (
		<TabPane tabId="3" className="border border-top-0">
			<Spelling />
		</TabPane>
	);

	// ? ---
	const tab4 = state.t4 && (
		<NavItem style={{ cursor: "pointer" }}>
			<NavLink
				className={classnames({
					active: activeTab === "4",
				})}
				onClick={() => {
					toggle("4");
				}}
			>
				<FaChartBar size={24} data-tip={strings.tab4} data-for="fcb" />
				<MyToolTip id="fcb" type="info" />
			</NavLink>
		</NavItem>
	);

	const tabpen4 = state.t4 && (
		<TabPane tabId="4" className="border border-top-0">
			<MyChart />
		</TabPane>
	);

	// ? ---
	const tab5 = state.t5 && (
		<NavItem style={{ cursor: "pointer" }}>
			<NavLink
				className={classnames({
					active: activeTab === "5",
				})}
				onClick={() => {
					toggle("5");
					document.getElementById("text").value = "";
				}}
				onDragOver={(ev) => ev.preventDefault()}
				onDrop={(ev) => {
					ev.preventDefault();
					let data = ev.dataTransfer.getData("text");
					glob.f(data);
				}}
			>
				<MdRecordVoiceOver
					size={24}
					data-tip={strings.tab5}
					data-for="mrvo"
				/>
				<MyToolTip id="mrvo" type="info" />
			</NavLink>
		</NavItem>
	);

	const tabpen5 = state.t5 && (
		<TabPane tabId="5" className="border border-top-0">
			<PlayerApp />
		</TabPane>
	);

	// ? ---
	const tab6 = state.t6 && (
		<NavItem style={{ cursor: "pointer" }}>
			<NavLink
				className={classnames({
					active: activeTab === "6",
				})}
				onClick={() => {
					toggle("6");
				}}
			>
				<MdImportExport
					size={24}
					data-tip={strings.tab6}
					data-for="mie"
				/>
				<MyToolTip id="mie" type="info" />
			</NavLink>
		</NavItem>
	);

	const tabpen6 = state.t6 && (
		<TabPane tabId="6" className="border border-top-0">
			<ImportExport />
		</TabPane>
	);

	// ? ---
	const tab7 = state.t7 && (
		<NavItem style={{ cursor: "pointer" }}>
			<NavLink
				className={classnames({
					active: activeTab === "7",
				})}
				onClick={() => {
					toggle("7");
				}}
			>
				<FaLink size={24} data-tip={strings.tab7} data-for="fl" />
				<MyToolTip id="fl" type="info" />
			</NavLink>
		</NavItem>
	);

	const tabpen7 = state.t7 && (
		<TabPane tabId="7" className="border border-top-0">
			<Links />
		</TabPane>
	);

	// ? ---
	const toggleDr = () => setOpen(!dropdownOpen);

	// ? ---
	const toggle = (tab) => {
		if (activeTab !== tab) setActiveTab(tab);
	};

	// ? ---
	if (!localStorage.guest) {
		localStorage.guest = JSON.stringify([
			{
				df: true,
				ex:
					"There were several gaps in my education. (Բերված է օրինակի համար։ Հարմար պահի այն կարող եք ջնջել բառատետրից։)",
				n: 0,
				p: 0,
				pos: "noun",
				pr: "/ɡæp/",
				time: 1611731372693,
				top: false,
				tr: "ճեղք, բացվածք",
				word: "gap",
			},
			{
				df: false,
				ex:
					"Prior to the Flood, many humans lived for centuries. (Բերված է օրինակի համար։ Հարմար պահի այն կարող եք ջնջել բառատետրից։)",
				n: 0,
				p: 0,
				pos: "noun",
				pr: "/ˈhjuːmən/",
				time: 1612876411235,
				top: false,
				tr: "մարդ",
				word: "human",
			},
		]);
	}

	// ? ---
	useEffect(() => {
		setSettings(state);
		// eslint-disable-next-line
	}, [state]);

	// ? ---
	return (
		<div id="App">
			<div id="vernagir">
				<h2> {strings.title} </h2>
			</div>

			<div>
				<ButtonDropdown isOpen={dropdownOpen} toggle={toggleDr}>
					<DropdownToggle outline color="info" id="settingsIcon">
						<FaCog size="24" />
					</DropdownToggle>
					<DropdownMenu className="mr-4">
						<DropdownItem header className="text-center text-info">
							{strings.tab8}
						</DropdownItem>

						<DropdownItem divider />

						<DropdownItem header>{strings.tab8_1}</DropdownItem>
						<div className="py-1 px-3">
							<FormGroup>
								<CustomInput
									type="radio"
									name="customRadio"
									id="r1"
									label={
										<Flags.GB
											style={{
												width: "30px",
												heigth: "20px",
											}}
										/>
									}
									// defaultChecked
									checked={lang}
									onChange={() => setLang(!lang)}
								/>
								<CustomInput
									type="radio"
									name="customRadio"
									id="r2"
									label={
										<Flags.AM
											style={{
												width: "30px",
												heigth: "20px",
											}}
										/>
									}
									checked={!lang}
									onChange={
										() => setLang(!lang)
										// setLang(lang === "AM" ? true : false)
									}
								/>
							</FormGroup>
						</div>

						<DropdownItem divider />

						<div className="p-2">
							<CustomInput
								type="switch"
								id="tab2"
								label={<BiBookAdd size={24} />}
								onChange={() => {
									if (activeTab === "2") toggle("1");
									dispatch({ type: "tab2" });
								}}
								checked={state.t2}
							/>
						</div>

						<div className="p-2">
							<CustomInput
								type="switch"
								id="tab3"
								label={<FaSpellCheck size={24} />}
								onChange={() => {
									if (activeTab === "3") toggle("1");
									dispatch({ type: "tab3" });
								}}
								checked={state.t3}
							/>
						</div>

						<div className="p-2">
							<CustomInput
								type="switch"
								id="tab4"
								label={<FaChartBar />}
								onChange={() => {
									if (activeTab === "4") toggle("1");
									dispatch({ type: "tab4" });
								}}
								checked={state.t4}
							/>
						</div>

						<div className="p-2">
							<CustomInput
								type="switch"
								id="tab5"
								label={<MdRecordVoiceOver size={24} />}
								onChange={() => {
									if (activeTab === "5") toggle("1");
									dispatch({ type: "tab5" });
								}}
								checked={state.t5}
							/>
						</div>

						<div className="p-2">
							<CustomInput
								type="switch"
								id="tab6"
								label={<MdImportExport size={24} />}
								onChange={() => {
									if (activeTab === "6") toggle("1");
									dispatch({ type: "tab6" });
								}}
								checked={state.t6}
							/>
						</div>

						<div className="p-2">
							<CustomInput
								type="switch"
								id="tab7"
								label={<FaLink size={22} />}
								onChange={() => {
									if (activeTab === "7") toggle("1");
									dispatch({ type: "tab7" });
								}}
								checked={state.t7}
							/>
						</div>
					</DropdownMenu>
				</ButtonDropdown>
			</div>

			<div id="nav">
				<Nav tabs className="nav-justified">
					<NavItem style={{ cursor: "pointer" }}>
						<NavLink
							className={classnames({
								active: activeTab === "1",
							})}
							onClick={() => {
								toggle("1");
							}}
						>
							<BiBookReader
								size={24}
								data-tip={strings.tab1}
								data-for="bbr"
							/>
							<MyToolTip id="bbr" type="info" />
						</NavLink>
					</NavItem>
					{tab2}
					{tab3}
					{tab4}
					{tab5}
					{tab6}
					{tab7}{" "}
					{/* <NavItem style={{ cursor: "pointer" }}>
            <NavLink
                className={classnames({
                    active: activeTab === "6",
                })}
                onClick={() => {
                    toggle("6");
                }}
            >
                <FaUserTie size={24}/>
            </NavLink>
        </NavItem> */}
				</Nav>

				<TabContent activeTab={activeTab}>
					<TabPane tabId="1" className="border border-top-0">
						<WordList />
					</TabPane>
					{tabpen2}
					{tabpen3}
					{tabpen4}
					{tabpen5}
					{tabpen6}
					{tabpen7}
					{/* <TabPane tabId="6" className="border border-top-0">
            <Users />
        </TabPane> */}
				</TabContent>
			</div>
		</div>
	);
}

// todo
export default App;
