import React from "react";
import { CustomInput, FormGroup, Button } from "reactstrap";
import saveFile from "save-as-file";
import { useLng } from "./CONTEXT/LngContext";

// todo
const jsonToFILE = (data) => {
	const file = new File([localStorage.guest], { type: "application/json" });
	saveFile(file, "wordbook.json");
};

// todo
const readFile = (s1, s2, s3, s4) => {
	// ? ---

	if (document.querySelector("#file-input").files.length === 0) {
		alert(s1);
		return;
	}

	// ? --- file selected by user
	const file = document.querySelector("#file-input").files[0];

	// ? ---
	let reader = new FileReader();

	reader.readAsText(file, "utf-8");
	reader.onload = function () {
		const PROPS = [
			"df",
			"ex",
			"n",
			"p",
			"pos",
			"pr",
			"time",
			"top",
			"tr",
			"word",
		];
		try {
			let parceObject = JSON.parse(reader.result);
			const wordProps = Object.getOwnPropertyNames(parceObject[0]);
			if (wordProps.length !== PROPS.length) {
				alert(s2);
				return false;
			}
			for (var i = 0; i < PROPS.length; i++) {
				if (PROPS[i] !== wordProps[i]) return false;
			}
		} catch {
			return alert(s3);
		}

		localStorage.guest = reader.result;
		alert(s4);
	};
};

// todo
function IpmortExport() {
	// ? ---
	const strings = useLng();

	// ? ---
	return (
		<div className="w-75 mx-auto py-3 mt-3">
			<div className="border p-3">
				<FormGroup>
					<CustomInput
						type="file"
						id="file-input"
						name="customFile"
						label={strings.tab6_1}
					/>
				</FormGroup>

				<Button
					id="read-button"
					color="danger"
					onClick={() =>
						readFile(
							strings.tab6_2,
							strings.tab6_3,
							strings.tab6_4,
							strings.tab6_5
						)
					}
					block
				>
					{"File  \u21E8  Wordbook"}
				</Button>
			</div>

			<br />

			<div className="border p-3">
				<Button
					color="success"
					onClick={() => jsonToFILE(localStorage.guest)}
					block
				>
					{" Wordbook  \u21E8  File"}
				</Button>
			</div>
		</div>
	);
}

// todo
export default IpmortExport;
