import React, { useContext, useState } from "react";

// * --- sa xndri 1-in masn e. haytararel context-y.
const GlobalContext = React.createContext();

// todo
// * --- sa xndri 2-rd masn e. stextsel context-i client-y.

export function useGlobal() {
	return useContext(GlobalContext);
}

// todo
// * --- sa xndri 3-rd masn e. stextsel contexti providery.

export function GlobalProvider({ children }) {
	// ? ---
	const [globals, setGlobals] = useState({ f: null });

	// ? ---
	return (
		<GlobalContext.Provider value={[globals, setGlobals]}>
			{children}
		</GlobalContext.Provider>
	);
}
